@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&family=Roboto:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@300,400;600&display=swap);

/**************************/
/****** STYLES PAGE ******/
/*************************/


/***** PRESETS *****/
body {
    margin: 0;
    padding: 0;
    min-height: 850px;
}
* {
    margin: 0;
    padding: 0;
}

p {
    margin-top: 5px;
    margin-bottom: 5px;
    font-family: 'Barlow', sans-serif;
    font-weight: 300;
    color: #383838;
}

h1, h2, h3, h4, h5, li {
    font-family: 'Barlow', sans-serif;
    color: #383838;
}

h1 {
    color: white;
    font-size: 64px;
    text-align: center;
    font-weight: 300;
}

h2 {
    margin-top: 20px;
    margin-bottom: 50px;
    font-size: 36px;
    font-weight: 600;
}

h3 {
    color: white;
    margin-top: 30px;
    margin-bottom: 30px;
    font-weight: 300;
    font-size: 24px;
    text-align: center;
}

h4 {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 36px;
/*    font-weight: 300;*/
    
}

h5 {
    font-size: 18px;
    color: #A1A493;
    font-weight: 300;
}

/* stars */
.checked {
  color: #D9CC5E;
}


/* END OF PRESETS */


/****** NAVBAR *******/
nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
/*    border-bottom: solid #999 2px;*/
    box-shadow: 0 8px 5px -10px #999;
    position: fixed;
    top: 0;
    background-color: white;
    margin-bottom: 50px;

}

.logo {
    margin-left: 8%;
}

.cart-icon {
    position: relative;
}

.notif {
    position: absolute;
    background-color: #B74848;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
    margin-bottom: 15px;
}

.notif > p {
    color: white;
    font-size: 10px;
}


.menu {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    font-family: 'Roboto Condensed', sans-serif;
/*    border: 1px solid hotpink;*/
    
}

.menu > a {
    width: 100px;
/*    border: 1px solid blue;*/
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: #79796B;
}
/* END OF NAVBAR */

section {
    margin-top: 50px;
}

/****** Footer *******/
.footer {
    display: flex;
    width: 100%;
    height: 112px;
    background-color: #383838;
    color: white;
    justify-content: center;
}

.footer-info {
    width: 80%;
/*    border: solid 1px pink;*/
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    color: white;
}

.footer-info > p {
    color: white;
}

.footer-info > p > a {
    color: #49A6DA;
}

/* END OF FOOTER */


/***** END OF STYLES *****/

/**************************/
/******* INDEX PAGE *******/
/**************************/


/***** Special Buttons *****/
.shop-now-button {
    text-decoration: none;
    color: #383838;
    background-color: white;
    width: 160px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.shop-now-button > a {
    text-decoration: none;
    color: #383838;
}
/* End of Special Buttons */

/***** Hero Image *****/
.hero {
    background-image: url(/homework_6/static/media/home-hero.42f1358f.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 650px;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.overlay {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
/* End of Hero Image */

/***** Featured Section *****/

.featured {
    display: grid;
    grid-template-columns: 10% 80% 10%;
    grid-template-rows: 150px 500px;
/*    grid-gap: 20px;*/
    height: 700px;
/*    border: 1px solid black;*/
    padding-top: 50px;
    padding-bottom: 50px;
}

.featured > .section-header {
    grid-column-start: 1;
    grid-column-end: 4;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 70px;
}

.featured-wrapper {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    display: flex;
    width: 100%;
    justify-content: space-between;
    flex-direction: row;
    align-items: flex-start;
/*    border: 1px solid gray;*/
}

.featured-card {
    width:100%;
/*    border: 1px solid green;*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    
}
.featured-card > h5 {
    width: 50%;
}

.featured-card > img {
    width: 80%;
    
}
/* End of Featured Section */
/*********************************/
/****** SHOPPING CART PAGE ******/
/*******************************/

.bold {
    font-weight: 600;
}

.shopping-cart-container {
    display: grid;
    grid-template-columns: 10% 60% 20% 10%;
}

.cart-wrapper {
    grid-column: 2/3;
    padding-top: 75px;
    padding-bottom: 150px;
}

.items-card {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* border: 1px solid pink; */
    width: 80%;
    margin-bottom: 80px;
    /* box-shadow: 0px 5px 8px 0px #ccc; */
    padding: 20px;
}

.item-info {
    display: flex;
    flex-direction: row;
    width: 100%;
    
    
    /* border: 1px solid pink; */
}

.item-text {
    /* border: 1px solid blue; */
    width: 100%;
    display: flex;
    flex-direction: column;
    /* align-items: space-between; */
    /* align-items: flex-end; */
    padding-right: 20px;
    padding-left: 20px;
}

.detail {
    self-align: flex-end;
}

.title-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* border: 1px solid blue; */
    width: 95%;
}

.title {
    font-size: 24px;
}
.price {
    font-size: 24px;
    font-weight: 300;
}

.delete {
    background-color: #EBEAE3;
    height: 35px;
    width: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin-top: 5px; */
    border: none;
    
}
.checkout {
    background-color: #614747;
    height: 45px;
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin-top: 5px; */
    border: none;
    font-size: 20px;
    margin-top: 85px;
    margin-bottom: 50px;
}

.checkout > p {
    color: white;
}

h4 {
    margin-top: 0px;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 300;
}

.totals > h4 {
    margin-bottom: 30px;
    font-size: 20px;
}

.description {
    margin-top: 30px;
    margin-bottom: 50px;
}


/**************************/
/***** PRODUCTS PAGE ******/
/**************************/

/***** General Products Section *****/
.products {
    display: grid;
    grid-template-columns: 10% 80% 10%;
}

.products-wrapper {
    grid-column: 2/3;
    padding-top: 75px;
    padding-bottom: 150px;
}
/* End of General Products Section */

/***** Filters *****/
.filters {
    display: flex;
    flex-direction: row;
    width: 45%;
    justify-content: space-between;
    margin-bottom: 125px;
    font-family: 'Roboto Condensed', sans-serif;

}

.filter-item {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 25%;
}

.filter-label {
    margin-right: 5px;
}
/* End of Filters */

/***** Product Gallery *****/
.product-gallery {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.product-card {
    width: 26%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 70px;
}
.product-card > a {
    width: 100%;
    
}

.product-card > a > img {
    width: 100%;
    
}
.product-text > .name-and-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.product-text > .rating-and-cart {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.product-text > .rating-and-cart > .rating {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
/* End of Product Gallery */

/***** Button *****/
.add-to-cart {
    border: none;
    outline: none;
    cursor: pointer;
    background-color: white;
}
/*********************************/
/***** PRODUCT DETAILS PAGE ******/
/*********************************/

/***** Details Section *****/
.details {
    display: grid;
    grid-template-columns: 10% 80% 10%;
    
}
.details-wrapper {
    grid-column-start: 2;
    grid-column-end: 3;   
    padding-top: 75px;
    padding-bottom: 150px;
}


.detail-heading {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
/*    border: solid 1px hotpink;*/
    margin-bottom: 50px;
}
.detail-title {
    width: 400px;
    display: flex;
    flex-direction: column;
/*    border: solid 1px green;*/
}

.detail1 {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.detail1 > h2 {
    margin-bottom: 10px;
}

.detail1 > p {
/*    asdfa;skdljfa;slkj*/
    margin-top: 20px;
    font-size: 24px;
    font-weight: 300;
}

.detail2 {
    display: flex;
    flex-direction: row;
}
/* End of Details Section */

/***** Shopping Cart *****/
.shopping-cart-button {
    background-color: #242323;
    width: 70px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
}
/* End of Shopping Cart */

/***** Gallery *****/
.gallery {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 20px;
    width: 100%;
    max-height: 600px;
/*    border: 1px pink solid;*/
}

.img-crop {
    grid-column: 1/6;
    width: 100%;
    height: 450px; 
    overflow: hidden;
    
}
/* redownload images later */
/* .big_image { */
    /* grid-column: 1/6; */
    /* width: 100%; */
    /* margin-top: -300px; */
/* } */

.gallery > .small_image1 {
    grid-column: 1/2;
}
.gallery > .small_image2 {
    grid-column: 2/3;
}
/* End of Gallery */

/***** Product Details *****/

.detail-columns {
/*    border: 1px solid blue;*/
    display: flex;
    flex-direction: row;
}

.detail-columns > .left-column {
/*    border: 1px solid purple;*/
    width: 60%;
}
.detail-columns > .right-column {
/*    border: 1px solid red;*/
    width: 40%;
    padding-left: 50px;
}

.detail-columns > .right-column > .description > ul > li {
    margin-left: 15px;
}


.name {
    font-family: 'Barlow', sans-serif;
    font-weight: 300;
    font-size: 24px;
    margin-bottom: 10px;
}

.name-and-stars {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.review-card {
    display: flex;
    flex-direction: column;
    margin-top: 25px;
    margin-bottom: 65px;
    height: 150px;
/*    border: 1px blue solid;*/
}

.review-num {
   font-family: 'Barlow', sans-serif;
}

.review-card > .title {
   font-weight: bold;
}

.description > h4 {
    font-size: 24px;
}

.product-color-wrapper > h5 , .size > h5 {
    color: black;
    font-weight: bold;
}

.size {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 125px;
    height: 50px;
}

.size > h3 {
    font-family: 'Roboto Condensed', sans-serif;
}

.size > img {
    height: 7px;
}

/****** Product Buttons ******/
.shopping-cart-button {
    
}
.product-color-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 220px;
    height: 50px;
}

.product-color {
    width: 35px;
    height: 25px;
    cursor: pointer;
}
.product-color {
    /* border: 1px solid black; */
    outline: none;
}

.size-btn {
    border: 1px solid black;
    outline: none;
    /* width: 35px; */
    height: 25px;
    margin-right: 3px;
    margin-left: 3px;
    padding-right: 5px;
    padding-left: 5px;
    background-color: black;
    color: white;
    cursor: pointer;

}
*:focus {
    outline: 0;
}

/* End of Product Buttons */

/****** Product Coloring ******/

/* Strawberry */
